<template>
  <el-container>
    <el-header style="width: 200px;height: 300px">
      <scenarioFilter @indexSelect="listByFilter" ref="scenarioFilter"></scenarioFilter>
    </el-header>
    <el-main>
      <scenarios class="scenarios-area" ref="scenariosArea"></scenarios>
    </el-main>
  </el-container>
</template>

<script>
  import ScenarioFilter from './ScenarioFilter'
  import Scenarios from './Scenarios'

  export default {
    name: 'ScenarioIndex',
    components: {ScenarioFilter, Scenarios},
    methods: {
      listByFilter () {
        var _this = this
        var did = this.$refs.scenarioFilter.did
        var tid = this.$refs.scenarioFilter.tid
        var oid = this.$refs.scenarioFilter.oid
        var nid = this.$refs.scenarioFilter.nid
        var url = 'filter/' + did + '/' + tid + '/' + oid + '/' + nid + '/scenarios'
        this.$axios.get(url).then(resp => {
          if (resp && resp.data.code === 200) {
            _this.$refs.scenariosArea.scenarios = resp.data.result
            _this.$refs.scenariosArea.currentPage = 1
          }
        })
      }
    }
  }
</script>

<style scoped>
  .scenarios-area {
    width: 1200px;
    margin-left: auto;
    margin-right: auto;
  }
</style>



